import React, { Component, Fragment } from 'react';
import { TabPane, Row, Col, Card, CardBody, CardTitle, CardText } from 'reactstrap';

export default class Berita extends Component {
    render() {
        return (
            <Fragment>
                <TabPane tabId="1">
                    <Row className="mt-4">
                        <Col>
                            <Card>
                                <CardBody className="bl-unj">
                                    <CardTitle className="text-muted"><b>Berita satu</b></CardTitle>
                                    <CardText>This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</CardText>
                                    <CardText>
                                        <small className="text-muted">Last updated 3 mins ago</small>
                                    </CardText>
                                </CardBody>
                            </Card>
                            <Card className="mt-3">
                                <CardBody className="bl-unj">
                                    <CardTitle className="text-muted"><b>Berita dua</b></CardTitle>
                                    <CardText>This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</CardText>
                                    <CardText>
                                        <small className="text-muted">Last updated 3 mins ago</small>
                                    </CardText>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </TabPane>
            </Fragment>
        )
    }
}
