import React, { Component, Fragment } from 'react';
import { TabPane, Row, Col, Form, FormGroup, Label, Input, Button } from 'reactstrap';
// import { Document, Page } from 'react-pdf';
import kalender from './kalenderAkademik.pdf';

export default class Kalender extends Component {

    render() {
        return (
            <Fragment>
                <TabPane tabId="2">
                    <Row className="">
                        <Col sm="12">
                            <Form className="mb-4">
                                <FormGroup row>
                                    <Col lg="3" sm="6" md="12" className="pt-3">
                                        <Label for="exampleSelect">Pilih Semester :</Label>
                                        <Input type="select" name="select" id="exampleSelect" className="form-control-sm">
                                            <option>107 - 1 2017/2018</option>
                                            <option>106 - 3 2016/2017</option>
                                            <option>105 - 1 2015/2016</option>
                                            <option>104 - 3 2014/2015</option>
                                            <option>103 - 1 2013/2014</option>
                                            <option>102 - 3 2012/2013</option>
                                        </Input>
                                    </Col>
                                    <Col sm="3" className="pt-3 mt-auto">
                                        <Button color="info" className="btn-sm"><i className="fas fa-search"></i> Proses</Button>
                                    </Col>
                                </FormGroup>
                            </Form>
                        </Col>
                        <Col sm="12" className="text-center">
                            <embed src={kalender} type="application/pdf" width="100%" height="600px" />
                            {/* <a href={kalender} target="_blank" rel="noopener">Kalender</a> */}
                            {/* <a target="_blank" rel="noopener noreferrer" href={kalender} >Unduh PDF </a> */}
                        </Col>
                    </Row>
                </TabPane>
            </Fragment>
        )
    }
}
